import Vue from 'vue'
import VueRouter from 'vue-router'
import GithubView from '../views/GithubView.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: GithubView
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
